import React from 'react';
export const Product=(props)=>{
    const mystyle={width:'18rem'};
    return(
        <div className="row">
        <div className="col-sm-6">
        <div className="card" mystyle="width: 18rem;">
        <img className="card-img-top" src={props.item.url} alt="Card image cap" />
        <div className="card-body">
        <h1 className="card-title">{props.item.name}</h1>
        <p className="card-text">{props.item.price}</p>
        <a href="#" className="btn btn-primary">Add to cart</a>
        </div>
        </div>
        </div>
        <div className="col-sm-6">
        <h2>Product details</h2>
        <p>4gB ram,256gB SSD</p>

        </div>
        </div>
        
    )
}